import {Component, OnDestroy, OnInit} from '@angular/core';
import {SharedServiceService} from '../../shared-service.service';
import {Router} from '@angular/router';
import {ApiService} from '../../api.service';
import {AlertController} from '@ionic/angular';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.page.html',
  styleUrls: ['./settings.page.scss'],
})
export class SettingsPage {
  handlerMessage = '';
  userMobile = '';
  userName = '';
  userRole = '';
  constructor(private adminService: ApiService, private alertController: AlertController, public router: Router, private sharedService: SharedServiceService) { }

  ionViewWillEnter() {
    this.sharedService.onSettingEvent.next(true);
    this.getUserDetails();
  }

  onLogout() {
    this.sharedService.removeAllUserDetails();
    this.router.navigate(['']);
  }


  getUserDetails() {
    this.sharedService.showSpinner.next(true);
    this.adminService.getUserDetails().subscribe(
        res => this.getUserDetailsSuccess(res),
        error => {
          this.adminService.commonError(error);
        }
    );
  }

  getUserDetailsSuccess(res) {
    this.userName = res.data.name;
    this.userRole = res.data.roleName;
    this.userMobile = res.data.mobileNo;
    this.sharedService.showSpinner.next(false);
  }


  async presentAlert() {
    const alert = await this.alertController.create({
      header: 'Do you want to Logout',
      cssClass: 'alert-popup',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            this.handlerMessage = 'Alert canceled';
          },
        },
        {
          text: 'OK',
          role: 'confirm',
          handler: () => {
            this.sharedService.removeAllUserDetails();
            this.onLogout();
            this.handlerMessage = 'Alert confirmed';
          },
        },
      ],
    });
    await alert.present();
  }

  // when Application will leave the page
  ionViewWillLeave() {
    this.sharedService.onSettingEvent.next(false);
  }

}
